.page-header.is-light .donate-menu-item {
  background-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--black);
}

.page-header.is-light .donate-menu-item:hover > a,
.page-header.is-light .donate-menu-item a:active,
.page-header.is-light .donate-menu-item a:focus,
.page-header.is-light .donate-menu-item:focus-within > a {
  background-color: var(--wp--preset--color--primary-state);
}
