.donation.is-style-dark {
  background-color: var(--wp--preset--color--grey-dark);

  .external-donate-wrapper {
    color: var(--wp--preset--color--white);
  }
}

.donation.is-style-bright {
  background-color: var(--wp--preset--color--primary);

  .donation-title,
  .donation-description,
  .donation-label,
  .donation-campaignLabel,
  .donation-campaignText,
  .donation-selectType,
  .donation-productSelect,
  .external-donate-wrapper {
    color: var(--wp--preset--color--black);
  }

  .donation-selectType label.is-active {
    background-color: var(--wp--preset--color--black);
    color: var(--wp--preset--color--white);
  }

  .donation-tabs div {
    border-bottom-color: var(--wp--preset--color--primary);

    &.is-active {
      border-bottom-color: var(--wp--preset--color--black);
      background-color: var(--wp--preset--color--black);
      color: var(--wp--preset--color--white);
    }
  }

  hr {
    border-bottom-width: 0;
  }

  .btn {
    background-color: var(--wp--preset--color--black);
    color: var(--wp--preset--color--white);

    &:hover {
      background-color: var(--wp--preset--color--black);
    }
  }
}

.donation-selectType.is-donation div:first-child label,
.donation-selectType.is-subscription div:last-child label {
  background-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--black);

  .is-style-bright & {
    background-color: var(--wp--preset--color--black);
    color: var(--wp--preset--color--white);
  }
}

.donation .donation-tabs div.is-active,
.donation.is-style-default .donation-tabs div.is-active,
.donation.is-style-dark .donation-tabs div.is-active {
  background-color: var(--wp--preset--color--primary);
  border-bottom-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--black);
}

.donation .btn {
  background-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--black);
}

.donation .btn:hover {
  background-color: var(--wp--preset--color--primary-state);
}

.donation .donation-selectType label.is-active {
  background-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--black);
}
