.wp-block-amnesty-petition,
.wp-block-amnesty-petition.is-style-default {
  background-color: var(--wp--preset--color--primary) !important;

  &.is-signed {
    background-color: var(--wp--preset--color--primary) !important;
    opacity: 0.6;
  }
}

.wp-block-amnesty-petition.is-style-light {
  background-color: var(--wp--preset--color--grey-lighter) !important;

  &.is-signed {
    background-color: var(--wp--preset--color--grey-lighter) !important;
    opacity: 0.6;
  }
}

.wp-block-amnesty-petition.is-style-dark-grey {
  background-color: var(--wp--preset--color--grey-dark) !important;
  color: var(--wp--preset--color--white);

  &.is-signed {
    background-color: var(--wp--preset--color--grey-dark) !important;
    opacity: 0.6;
  }

  .petition-title {
    color: var(--wp--preset--color--white);
  }
}
