.btn.btn--download.btn--primary {
  background-color: var(--wp--preset--color--primary);
  border: none;
}

.btn.btn--download.btn--primary:hover,
.btn.btn--download.btn--primary:focus,
.btn.btn--download.btn--primary:active {
  background-color: var(--wp--preset--color--primary-state);
  color: var(--wp--preset--color--black);
  border: none;
}

.btn.btn--download.btn--primary:hover::before,
.btn.btn--download.btn--primary:focus::before,
.btn.btn--download.btn--primary:active::before {
  content: "";
  background-image: url('../images/icon-download-arrow-dark.svg');
  width: 20px;
  height: 20px;
  display: inline-block;
  margin-right: 20px;
  background-size: contain;
  transform: translateY(2px);
}

.btn.btn--download.btn--dark:hover::before,
.btn.btn--download.btn--dark:focus::before,
.btn.btn--download.btn--dark:active::before {
  background-image: url('../images/icon-download-arrow-dark.svg');
}
