.wp-block-button.is-style-dark .wp-block-button__link {
  background-color: var(--wp--preset--color--grey-dark) !important;
  color: var(--wp--preset--color--white) !important;
  border: none !important;

  &:hover,
  &:active,
  &:focus {
    background-color: var(--wp--preset--color--grey-darker) !important;
  }
}
