.page-header.is-transparent-light .donate-menu-item a {
  background-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--black);
}

.page-header.is-transparent-light .donate-menu-item a:hover,
.page-header.is-transparent-light .donate-menu-item a:active,
.page-header.is-transparent-light .donate-menu-item a:focus {
  background-color: var(--wp--preset--color--primary-state);
}
